import request from '../request'
import { urls } from '../urls'

export function getJourneyAllianceInfoList () { // 
  return request({
    url: urls.getJourneyAllianceInfoList,
    needlessToken: true,
    data: {}
  })
}

export function getJourneyAllianceDetailList (params) { // 
  return request({
    url: urls.getJourneyAllianceDetailList,
    needlessToken: true,
    data: {
      ...params
    }
  })
}

