import request from '../request'
import { urls } from '../urls'

export function getJourneyProductInfoPage (params) { // '/linanJourney/journeyProduct/getJourneyProductInfoPage', // 
  return request({
    url: urls.getJourneyProductInfoPage,
    needlessToken: true,
    data: { ...params }
  })
}

export function getJourneyProductInfoById (params) { // '/linanJourney/journeyProduct/getJourneyProductInfoById', // 
  return request({
    url: urls.getJourneyProductInfoById,
    needlessToken: true,
    data: { ...params }
  })
}

/**
 * @param {type}  (01:; 02:)
 * @param {status}  (01:; 02:; 03:)
 */
export function getJourneyActivityPage (params) { // '/linanJourney/journeyActivity/getJourneyActivityPage', // 
  return request({
    url: urls.getJourneyActivityPage,
    needlessToken: true,
    data: { type: '02', ...params }
  })
}
