import request from '../request'
import { urls } from '../urls'

export function getJourneyRegionsList () { //  '/linanJourney/journeyLine/getJourneyRegionsList'
  return request({
    url: urls.getJourneyRegionsList,
    needlessToken: true,
    data: {
    }
  })
}

export function getJourneyPointListByRegionsCode (params) { //  '/linanJourney/journeyLine/getJourneyPointListByRegionsCode'
  return request({
    url: urls.getJourneyPointListByRegionsCode,
    needlessToken: true,
    data: {
      ...params
    }
  })
}

export function getRecommendJourneyLineList () { //  '/linanJourney/journeyLine/getRecommendJourneyLineList'
  return request({
    url: urls.getRecommendJourneyLineList,
    needlessToken: true,
    data: {
      orgId: localStorage.getItem('orgId')
    }
  })
}

