import MescrollUni from "@/components/mescroll-uni/mescroll-uni.vue";
import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";

const map = {
  data () {
    return {
      search: {
        pageNumbe: 1,
        keyword: ''
      },
      mescrollBottom: '0',
      upOption: {
        empty: {
          use: false
        },
        page: {
          size: 5 // ,10
        },
        textNoMore: "",
        noMoreSize: 10, // 10'-- END --'
      },
      dataList: []
    }
  },
  methods: {
    upCallback (page) {
      this.search.pageNumber = page.num
      this.getDataList()
    },
    downCallback (page) {
      this.mescroll.resetUpScroll()
    },
  },
  mixins: [MescrollMixin],
  components: {
    MescrollUni
  }
}

export default map







